.detailFooter_line{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 95%;
    margin: 0 auto;
    padding: 5px;
    border-bottom: 1px solid rgb(214, 214, 214);
    padding: 5px 0;
}
.detailFooter_line_side{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.title{
    font-size: 18px;
    font-weight: 600;
    color:rgb(235, 235, 235);
}
.detailFooter_line_number{
    font-size: 22px;
}